@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{padding:env(safe-area-inset)}body{background:repeat url(/markdown-previewer/static/media/background.383c3401.jpg);box-sizing:border-box;font-color:#000;font-family:Lora,serif;line-height:2em}button{background-color:inherit;border:none;color:#fff;outline:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}button:hover,button:active{color:#fff}button:focus{font-size:1.5em}code{background-color:#000;color:#fff;display:inline-block;font-weight:600;margin:0.1em;padding:0.5em}h1{border-bottom:0.1em dotted #000}a{color:#5f2626}blockquote{border-left:0.2em solid #000;padding-left:0.2em}table{border:0.1em solid #000}table th,table td{border:0.1em solid #000;box-shadow:0.1em 0.1em 0.2em #000;padding:0.2em}.bar{-webkit-align-items:center;align-items:center;background-color:#000;color:#fff;display:-webkit-flex;display:flex;height:2em;-webkit-justify-content:space-between;justify-content:space-between;padding:0 1%;width:98%}#editor-window{width:90%}#editor-window #editor{background-color:#fff;border:0;box-shadow:0 0.2em 1em #000;font-size:1.2em;height:100%;padding:0 1%;width:98%}#editor-window #editor:focus{border:0.1em dotted black;outline:none;width:97.5%}.editor-window-maxi{height:100vh}.preview-window #preview{background-color:#fff;box-shadow:0 0.2em 1em #000;margin:0;min-height:20em;overflow-wrap:anywhere;overflow:auto;padding:0 1%}.hidden{display:none}#editor-window,#preview-window{margin:1em auto;padding:0.2em}

